.btn {
  color: #fff;
  font-size: 15px;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

.btn:hover {
  background-color: #555;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

#quote-box {
  -webkit-box-shadow: 0px 0px 60px 18px rgba(0, 0, 0, 0.26);
  -moz-box-shadow: 0px 0px 60px 18px rgba(0, 0, 0, 0.26);
  box-shadow: 0px 0px 60px 18px rgba(0, 0, 0, 0.26);
  min-width: 300px;
  max-width: 600px;
  background-color: #fff;
  margin: auto;
  padding: 30px;
  border-radius: 5px;
}

#btn-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#quote-box #text {
  font-size: 25px;
  font-weight: 500;
}

#quote-box #author {
  font-size: 16px;
  text-align: right;
  margin-bottom: 20px;
}
